<template>
  <div class="my">
    <div class="my-bgimg" :style="{backgroundImage:`url(${userInfo.userBg})`}">
      <van-uploader v-if="isLogin" :after-read="upload" />
    </div>

    <div class="content">
      <div class="my-content">
        <div class="my-info clearfix">
          <div class="avatar fl">
            <img :src="userInfo.userImg" alt class="auto-img" />
          </div>
          <div class="info fl">
            <div class="info-nologin" v-if="!isLogin" @click="goLogin">未登录</div>
            <div class="name">{{userInfo.nickName}}</div>
            <div class="introduction">{{userInfo.desc}}</div>
          </div>
        </div>
        <div class="box">
          <van-cell
            :title="item.title"
            :to="item.to"
            is-link
            v-for="(item,index) in lists"
            :key="index"
          />
        </div>
      </div>
    </div>

    <div class="bg-img">
        <img class="auto-img" src="../../assets/images/shopbag_bg.png" alt="">
    </div>
  </div>
</template>

<script>
import { utils } from "../../assets/js/utils";
export default {
  data() {
    return {
      lists: [
        { title: "个人中心", to: { name: "Account" } },
        { title: "地址管理", to: { name: "Address" } },
        { title: "我的收藏", to: { name: "Like" } },
        { title: "安全中心", to: { name: "Secure" } }
      ],
      isLogin: false,

      userInfo: {},

      maxSize: 1 * 1024 * 1024
    };
  },
  created() {
    //查询用户信息
    this.getUserInfo();
  },
  methods: {
    goLogin() {
      this.$router.push({ name: "Login" });
    },

    //查询用户信息
    getUserInfo() {
      let tokenString = localStorage.getItem("CSTK");
      if (!tokenString) {
        return;
      }

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner"
      });

      this.axios({
        method: "GET",
        url: "/findMy",
        params: {
          appkey: this.appkey,
          tokenString
        }
      })
        .then(result => {
          this.$toast.clear();
          
          if (result.data.code == "A001") {
            if (result.data.result.length > 0) {
              this.isLogin = true;
              this.userInfo = result.data.result[0];
            }
          }
        })
        .catch(err => {
          this.$toast.clear();
          
        });
    },

    //上传背景图片
    upload(file) {
      let tokenString = localStorage.getItem("CSTK");
      if (!tokenString) {
        return;
      }

      //获取文件类型
      let type = file.file.type.split("/")[1];
      let imgTypes = ["png", "gif", "jpg", "jpeg"];

      if (imgTypes.indexOf(type) === -1) {
        this.$notify({
          message: "图片类型只支持" + imgTypes.join(","),
          color: "#fff",
          background: "#0C34BA"
        });

        return;
      }

      //大小
      if (this.maxSize < file.file.size) {
        this.$notify({
          message: "上传文件大小不能超过" + this.maxSize / 1024 + "KB",
          color: "#fff",
          background: "#0C34BA"
        });
        return;
      }
      let base64 = file.content.replace(/data:image\/[A-Za-z]+;base64,/, "");

      let data = utils.queryString({
        appkey: this.appkey,
        tokenString,
        imgType: type,
        serverBase64Img: base64
      });

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner"
      });

      this.axios({
        method: "POST",
        url: "updateUserBg",
        data
      })
        .then(result => {
          this.$toast.clear();
          

          if (result.data.code == "I001") {
            this.userInfo.userBg = result.data.userBg;
          }
        })
        .catch(err => {
          this.$toast.clear();
          
        });
    }
  }
};
</script>

<style lang="less" scoped>
.my {
  height: 100%;
}
.my-bgimg {
  height: 200px;
  background-color: #0c45a0;
  text-align: left;
  overflow: hidden;
  position: relative;
  top: 0;
  left: 0;
  background-position: center center;
  background-size: cover;
}

.content {
  margin-top: -120px;
  padding: 10px;
  position: relative;
  top: 0;
  left: 0;
}
.my-content {
  text-align: left;
  background-color: rgba(255, 255, 255, 0.8);
  padding: 20px;
  border-top-left-radius: 20px;
  border-top-right-radius: 20px;
}

.avatar {
  width: 80px;
  height: 80px;
  background: #999;
  border-radius: 50%;
  margin-right: 20px;
  img {
    border-radius: 50%;
  }
}
.info {
  .name {
    margin: 20px 0 10px 0;
    font-size: 22px;
    font-weight: bold;
  }
}
.box {
  margin-top: 30px;
}
/deep/.van-cell {
  background-color: #f5f5f5;
  margin-bottom: 10px;
  border-radius: 10px;
}

.info-nologin {
  font-size: 16px;
  color: #0c45a0;
  font-weight: bold;
  margin-top: 30px;
}
/deep/.van-uploader {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
/deep/.van-uploader__wrapper {
  width: 100%;
  height: 100%;
}
/deep/.van-uploader__upload {
  width: 100%;
  height: 100%;
  margin: 0;
  opacity: 0;
}

.bg-img{
    margin-top: 30px;
}
</style>